import Position from '../mixin/position';
import * as d3 from "d3";


import {
  $, 
  $$, 
  attr, 
  filter, 
  getIndex, 
  each, 
  hasClass, 
  html,
  includes, 
  index, 
  isInView, 
  scrollIntoView, 
  toggleClass, 
  unwrap, 
  wrapAll, 
  within, 
  width, 
  css, 
  toFloat, 
  addClass, 
  removeClass, 
  Transition, 
  assign, 
  merge, 
  append,
  trigger,
  find,
  empty
} from '../../util';

export default {
    mixins: [Position],
    props: {
      data: Object,
      dataTrain:Object,
      colors: Object,
    },

    data: {
      data:null,
      dataTrain:null,
      width:760,
      height:739,
      id:"mask_id",
      nav: '.data_list ul button',
      navMobile: '.data_list .active_item button',
      dataList: '.data_list',
      imageSvg : '#imagesSvg',
      imageSvgList : '#imagesSvg > g',
      activeClass:"mui_active",
      tooltipLayer:'#blockTooltip',
      chartSec:"",
      list: [
        'renderDefault',
        'renderTranMap',
        'renderLoadMap',
        'renderEduMap',
        'renderEcoMap',
      ],
      listID: [
        "trainMap",
        "loadMap",
        "eduMap",
        "economyMap"
      ]
    },
    computed: {
      nav({nav}){
        return $$(nav);
      },
      navMobile({navMobile}){
        return $(navMobile);
      },
      dataList({dataList}){
        return $(dataList);
      },
      imageSvg({imageSvg}){
        return $(imageSvg);
      },
      tooltipLayer({tooltipLayer}){
        return $(tooltipLayer);
      },
      imageSvgList({imageSvgList}) {
        return $$(imageSvgList);
      },
      data({data}){
        return new Map(mapPath.map((label, i) => [i, label]))
      },
      dataTrain({dataTrain}){
        return new Map(mapPath2.map((label, i) => [i, label]))
      },
      dataLoad(){
        return new Map(mapPath2.map((label, i) => [i, label]))
      },

    },
    
    connected(){
      const { $el, width, height } = this;
      const { select } = d3;
      
      const svg = this.svg = select(find('svg.map', $el));
      svg
        .attr('width', width)
        .attr('height', height)
        .attr('viewBox', `0 0 ${width} ${height}`);
        console.log(svg);
      this.group = select($('#map_bg'));
      this.renderDefault();

    },
    events: [
      {
        name: 'click',
        delegate(){
          return ".map_area";
        },
  
        handler(e) {
          trigger(this.$el, "select", [this, e.current]);
        }
      },
      {
        name: 'click',
        delegate(){
          return ".labels_walk";
        },
  
        handler(e) {
          this.selectLoad(e);
        }
      },
      {
        name: 'click',
        el(){
          return find('.close', this.tooltipLayer);
        },
  
        handler(e) {
          this.hideTooltip();
        }
      },
      
      {
        name: 'click',
        delegate(){
          return this.$props.nav
        },
  
        handler(e) {
          this.activeNav(e);
        }
      },

      {
        name: 'click',
        delegate(){
          return this.$props.navMobile
        },
  
        handler(e) {
          this.toggleNavMobile();
        }
      },
      // 
    ],

    methods: {
      render(data, className){
        const { svg, group, deletePath } = this;
        deletePath();
        if (data) {
          group.selectAll(".series")
          .data(data)
          .join("path")
          .attr("class", className)
          .attr('d', d => d[1].path)
          .attr('fill', d => d[1].fill)          
        } else{
          group.selectAll(".series")
          .attr("class", className)
        }

      },
      renderDefault() {
        const { svg, group, deletePath, data, activeClass } = this;
        let highlightPath;
        deletePath();
        const mask = group.append("mask")
          .attr('fill', 'white')
          .attr('id', "el_hilight")
          .selectAll(".mask_path")
          .data(data)
          .join("path")
          .attr('d', d => d[1].path)
          
          
        const series = group.selectAll(".series")
          .data(this.data)
          .join("path")
          .attr("class", "map_area")
          .attr('d', d => d[1].path)
          .attr('fill', d => d[1].fill)
          .attr('mask', (d, i) => `url(#el_hilight)`)
          .on("click", highlight)

        function highlight(e, d){
          const activePath = series.filter(s => d === s);
          if (!!highlightPath) {
            if (highlightPath !== activePath) {
              highlightPath
                .classed("mui_active", false)
            }else {
              return;
            }
          }

            
          activePath
            .classed("mui_active", true);
          highlightPath = activePath;
        }
        removeClass($$('.charts'), activeClass)
        this.showLabel(0);
      },
      renderTranMap(){
        const { render, dataTrain, imageSvg, activeClass} = this;
        render(dataTrain, 'train_map');
        addClass(find('#trainMap', imageSvg), activeClass)
        removeClass($('#barChart'), activeClass)
        removeClass($('#pieChart'), activeClass)
        this.showLabel(1);
      },
      renderLoadMap(){
        const { render, dataLoad, imageSvg, activeClass} = this;
        render(null, 'load_map');
        addClass(find('#loadMap', imageSvg), activeClass)
        removeClass($('#barChart'), activeClass)
        removeClass($('#pieChart'), activeClass)
        this.showLabel(2);
      },
      renderEduMap(){
        const { render, dataEdu, imageSvg, activeClass} = this;
        render(dataEdu, 'load_map');
        addClass(find('#eduMap', imageSvg), activeClass)
        addClass($('#barChart'), activeClass)
        removeClass($('#pieChart'), activeClass)
        this.showLabel(3);
      },
      renderEcoMap(){
        const { render, dataEco, imageSvg, activeClass} = this;
        render(dataEco, 'load_map');
        addClass(find('#economyMap', imageSvg), activeClass)
        removeClass($('#barChart'), activeClass)
        addClass($('#pieChart'), activeClass)
        this.showLabel(4);
      },
      deletePath() {
        const { group, imageSvg, imageSvgList, activeClass } = this;
        empty(group);
        removeClass(imageSvgList, activeClass);
        this.hideTooltip();
      },
      highlight(el) {
        console.log(el);
      },
      activeNav(e){
        const {nav, list, activeClass} = this;
        let index;
        each(nav, (el, i)=> {
          toggleClass(el, activeClass, el === e.current);
          if (el === e.current) {
            index = i;
            this.toggleNavMobile(el);
          }
        })
        this[list[index]]();
      },
      toggleNavMobile(el){
        const {navMobile, dataList, activeClass} = this;
        toggleClass(dataList, activeClass, !hasClass(dataList, activeClass));
        if (!!el) {
          // console.log(el);
          // empty(parent)
          html(find('.active_item', dataList), el.cloneNode(true))
        }

        
      },
      showTooltip(target) {
        const {tooltipLayer, activeClass} = this;
        addClass(tooltipLayer, activeClass);
        this.positionAt(tooltipLayer, target);
      },
      hideTooltip(){
        removeClass(this.tooltipLayer, this.activeClass);
      },
      showLabel(idx){
        const {activeClass} = this;
        removeClass($$('.labels'), activeClass)
        addClass($$('.labels')[idx], activeClass)
      },
      selectLoad(e){
        const target = e.current;
        const list = $$('.labels_walk');
        this.hideTooltip();
        list.forEach(el => {
          if(target !== el) {
            attr(el, "opacity", '0.5')
            removeClass(el, "mui_active")
            attr($$('circle', el), "opacity", '0')
            attr($$('.line', el), "stroke-width", '2')
            
          }else{
            attr(el, "opacity", '1')
            addClass(el, "mui_active")
            attr($$('circle', el), "opacity", '1')
            attr($$('.line', el), "stroke-width", '5')
            this.showTooltip(find('rect', el));
          }
        });
      }
    }
};


